.projectsBlock {
    height: 100vh;
    background-color: lightsteelblue;
}

.projectsContainer {
    flex-direction: column;
    justify-content: flex-start;

}

.title {
    background-color: beige;
}

.projects {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    box-sizing: border-box;
}

